.container {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  place-content: center;
  place-items: center;
  gap: 48px 46px;
  max-width: 100%;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 300px) {
  .container {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 1200px) {
  .container {
    padding: 50px 128px;
  }
}

.card {
  background-color: #fff;
